#header {
  background-color: #353059;
  grid-area: header;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .logo,
  .search,
  .notifications,
  .profile {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .logo {
    width: 200px;
    img {
      height: 64px;
    }
    .logo__title {
      font-size: 1.3em;
    }
  }
  .search {
    .search__button,
    .search__input {
      padding: 13px;
      border-radius: 30px;
      font-weight: bold;
    }
    .search__input {
      background-color: $primary_dark;
      border: 2px solid rgba($primary, 0.5);
      color: $primary_text_dark;
      padding-right: 150px;
      margin-right: 15px;
      
      &:focus {
        
      }
    }
    .search__button {
      transition: background 177ms linear;
      background-color: rgba(0, 0, 0, 0);
      border: 2px solid $search_border;
      color: inherit;
      &:hover {
        background-color: $primary_dark;
      }
    }
  }
  .notifications {
    .notifications__current {
      &::after {
        content: "|";
        padding: 0 15px 0 15px;
      }
    }
    .fa-layers {
      cursor: pointer;
      margin: 0 5px 0 5px;
      &:hover > svg path {
        color: $primary_text_dark_hover;
      }
    }
  }
  .profile {
    width: 200px;
  }
}
